import React from 'react';
import './Header.css';
import om from "../Images/om.jpg";
import sai_photo  from '../Images/sai-photo.jpg';
import Image from 'react-bootstrap/Image';


const Header = () =>{
    return(<>
    
        <div className='container-fluid header-color header-bg '>
                <div className='row p-2'>
                        <div className='col-lg-2 text-center'>
                            <img id='img' src={sai_photo} className='header-photo ' alt="sairam" />
                        </div>
                        <div className='col-lg-8 text-center zindex'>
                                <div className='header-text'>
                                    <small>      </small> <br />
                                    <small> </small>
                                    <small> <Image width='70' roundedCircle src={om}></Image></small>
                                    <small>    </small>
                                    <div>
                                        <span className='header-title'>        (.)  </span>
                                        <br />
                                        <small> .,  ,   561206  </small>
                                    </div>

                                </div>
                               
                                       
                        </div>
                      
                        
                </div>
               
        </div>
    
    </>)
}

export default Header;
